export function openExperienceTab() {
  var i, tabcontent, tablinks;

  // Get all elements with class="tabcontent" and hide them
  tabcontent = document.getElementsByClassName("tabcontent");
  tabcontent[0].style.display = "block";
}

window.openCity = (evt, cityName) => {
  try {
    // Declare all variables
    var i, tabcontent, tablinks;


    // Get all elements with class="tabcontent" and hide them
    tabcontent = document.getElementsByClassName("tabcontent");
    for (i = 0; i < tabcontent.length; i++) {
      tabcontent[i].style.display = "none";
    }

    // Get all elements with class="tablinks" and remove the class "active"
    tablinks = document.getElementsByClassName("tablinks");
    for (i = 0; i < tablinks.length; i++) {
      tablinks[i].className = tablinks[i].className.replace(" active", "");
    }
    console.log(evt)

    // Show the current tab, and add an "active" class to the button that opened the tab
    document.getElementById(cityName).style.display = "block";
    evt.currentTarget.className += " active";
    let index = 0;
    let projects = 0;
    let experience = 0;
    let scrollWidth = 0;
    let prevScrollWidth = 0;
    let nextScrollWidth = 0;
    if (document.querySelector('.tablinks').scrollWidth) {
      scrollWidth = (document.querySelector('.tablinks').scrollWidth * 70) / 100;
    }
    switch (cityName) {
      case 'benzy': { index = 9, projects = 10, experience = 4, scrollWidth = 0 }; break;
      case 'hummingwave': { index = 5, experience = 2.7, projects = 9, scrollWidth = scrollWidth }; break;
      case 'perpule': { index = 3, experience = 2, projects = 5, scrollWidth = scrollWidth * 2 }; break;
      case 'indisparrow': { index = 1, experience = 0.6, projects = 1, scrollWidth = scrollWidth * 3 }; break;
    }

    let leafIndex = 0;
    switch (cityName) {
      case 'benzy': leafIndex = 8; break;
      case 'hummingwave': leafIndex = 5; break;
      case 'perpule': leafIndex = 3; break;
      case 'indisparrow': leafIndex = 1; break;
    }
    stem = Array.from(document.querySelectorAll('.growth .stem'));
    leaf = Array.from(document.querySelectorAll('.growth .leaf'));

    stem = stem.filter((s, idx) => {
      if (idx <= index) {
        return s
      } else {
        s.style.opacity = 0;
      }
    })
    leaf = leaf.filter((l, idx) => {
      if (idx <= leafIndex) {
        return l
      } else {
        console.log('opacity')
        l.style.opacity = 0;
      }
    })
    var timeline1 = anime.timeline({
      easing: 'easeInOutSine'
    })
    timeline1.add({
      targets: stem,
      opacity: 1,
      duration: 500,
      delay: function (el, i) { return i * 50 },
      direction: 'alternate',
      loop: false
    }).add({
      targets: leaf,
      opacity: 1,
      duration: 500,
      delay: function (el, i) { return i * 50 },
      direction: 'alternate',
      loop: false
    })

    var timeline = anime.timeline({
      easing: 'easeInSine',
    })
    timeline.add({
      targets: '.tabs__content',
      opacity: [0.2, 1]
    })
    console.log(scrollWidth)
    document.querySelector('.tab').scrollTo(scrollWidth, 0)
    animateProjectNumber(experience, projects)



  } catch (ex) {
    console.log(ex)
  }

}

// Animation
export function animate() {
  let grids = document.querySelectorAll('.animation div');
  let num = 0;
  const delay = time => new Promise(resolve => setTimeout(resolve, time))
  grids.forEach(grid => {
    delay(0).then(() => {
      setTimeout(() => {
        grid.style.backgroundColor = '#043B7C';
        grid.style.opacity = "1"
        console.log('time')
      }, num += 100)
    })

    delay(2000).then(() => {
      grid.style.backgroundColor = '#043B7C';
      grid.style.opacity = "0"
    })
  })
}

var myObject = {
  projects: 0,
  years: 0
}
export function animateProjectNumber(years, projects) {
  var projectNoEl = document.querySelector('.project_no');
  var yearNoEl = document.querySelector('.years_no');

  anime({
    targets: myObject,
    years: years,
    easing: 'linear',
    round: 10,
    update: function () {
      yearNoEl.innerHTML = myObject.years + "+";
    }
  });
  anime({
    targets: myObject,
    projects: projects,
    easing: 'linear',
    round: 10,
    update: function () {
      projectNoEl.innerHTML = myObject.projects + "+";
    }
  });
}

