window.scrollPage = (id) => {
  const scrollElement = window.document.scrollingElement || window.document.body || window.document.documentElement;
  let offsetHeight = window.findPosition(document.getElementById(id));
  let navigation_link = Array.from(document.querySelectorAll('.navigation__link'));
  let screenWidth = window.screen.width;
  navigation_link.filter(link => {
    if (link.innerHTML && link.innerHTML.trim().toLocaleLowerCase().includes(id)) {
      link.classList.add('active');
    } else {
      link.classList.remove('active');
    }
  })
  anime({
    targets: scrollElement,
    scrollTop: offsetHeight,
    duration: 1000,
    easing: 'easeInOutQuad'
  })

  // Close menu bar
  if (screenWidth <= 1024) {
    document.querySelector('.navigation__logo--close').style.display = 'none';
    document.querySelector('.navigation__logo--open').style.display = 'block';
    anime({
      targets: document.querySelector('.navigation__list'),
      width: ['100%', '20%'],
      opacity: 0,
      duration: 1000,
      complete: function () {
        document.querySelector('.navigation__list').style.display = 'none';
      }
    })
  }

}

window.findPosition = (obj) => {
  var currenttop = 0;
  let screenWidth = window.screen.width;

  if (obj.offsetParent) {
    do {
      currenttop += obj.offsetTop;
    } while ((obj = obj.offsetParent));
    if (screenWidth <= 1024) {
      return [currenttop - 100]
    } else {
      return [currenttop]
    }
  }
}

