function activateSection(pageId) {
  const scrollPosition = window.scrollY || window.scrollTop || document.getElementsByTagName("html")[0].scrollTop;
  const section = findPosition(document.getElementById(pageId))
  const sectionOffset = document.getElementById(pageId).offsetHeight;
  if (scrollPosition >= section && scrollPosition <= (parseInt(section) + sectionOffset)) {
    setPageAnimation(pageId);
    setActiveNavigation(pageId);
  } else {
    removeActiveNavigation(pageId)
  }
}

