let toggleMenu = false;
document.querySelector('.navigation__logo').addEventListener('click', () => {

  toggleMenu = !toggleMenu;
  console.log(toggleMenu)
  if (toggleMenu) {
    document.querySelector('.navigation__logo--close').style.display = 'block';
    document.querySelector('.navigation__logo--open').style.display = 'none';
  } else {
    document.querySelector('.navigation__logo--close').style.display = 'none';
    document.querySelector('.navigation__logo--open').style.display = 'block';
  }
  let width = [];
  let opacity = [];
  /* Open Menu */
  if (toggleMenu) {
    width = ['0%', '100%'];
    opacity = [0.8, 1];
  } else {
    width = ['100%', '0%'];
    opacity = [1, 0];
  }

  anime({
    targets: document.querySelector('.navigation__list'),
    width: width,
    opacity: opacity,
    duration: 1000,
    begin: function () {
      if (toggleMenu) {
        document.querySelector('.navigation__list').style.display = 'block';
      }

    },
    complete: function () {
      if (toggleMenu) {
        document.querySelector('.navigation__list').style.display = 'block';
      } else {
        document.querySelector('.navigation__list').style.display = 'none';
      }
    }

  })
})

